import React from 'react'
import Banner from '../images/Group-min-removebg.png'
import { Button } from '@mui/material'

function BannerComponent() {
  return (
    <div className='Banner'>
        <div className="Banner-img">
            <img src={Banner} alt="" />
        </div>
        <div className="Banner-text">
            <h1 className='Banner-Title'>
                retirken ren, liderlik et, <br />
                ba kur, geli.
            </h1>
            <p className='Banner-subtext'>
            Mobimenti, genlerin kariyer yolculuunu destekleyen bir mentor a oluturuyor.
            <br /><br /> Mentor olarak daha etkin bir lider, geni bir a ve gl bir kariyer ina etmeye hazr ol!

            </p>
            <div className="Banner-Button">
            <Button variant="contained" sx={{borderRadius:'20px', width:"150px", height:"50px", background: "linear-gradient(to left,#7978FF,#1D1CE5)"}} >Mentor Ol</Button>
            </div>
        </div>
    </div>
  )
}

export default BannerComponent