import React from 'react'
import World from '../images/world-min.jpg'
import Select from '../images/select-min.jpg'
import Meeting from '../images/meeting-min.jpg'

function CardComponent() {
  return (
    <div className='Card-Component'>
        <div className="Card">
            <div className="Card-img">
                <img src={World} alt="" />
            </div>
            <div className="Card-Text">
                <div className="Card-Title"><h1>Mentorlar Kefet</h1></div>
                <div className="Card-Subtext">
                    <p>Kariyer admlarnda ilerlemek iin istediin kategorideki 
                    mentorlar kefet!</p>
                </div>
            </div>
        </div>
        <div className="Card">
            <div className="Card-img">
                <img src={Select} alt="" />
            </div>
                <div className="Card-Text">
                    <div className="Card-Title"><h1>ncele ve Se</h1></div>
                    <div className="Card-Subtext">
                        <p>Mentorlar incele ve birini se!</p>
                    </div>
                </div>
        </div>
        <div className="Card">
            <div className="Card-img">
                <img src={Meeting} alt="" />
            </div>
                <div className="Card-Text">
                    <div className="Card-Title">
                        <h1>Randevu Al,
                        Grmeye Bala</h1>
                    </div>
                    <div className="Card-Subtext">
                        <p>
                        Randevunu alarak mentorunla
                        grmeye bala!
                        </p>
                    </div>
                </div>
        </div>
    </div>
  )
}

export default CardComponent