import React from 'react'
import { Link } from 'react-router-dom'
import Instagram from '../images/instagram_logo.png'
import Linkedin from '../images/linkedin_logo.png'

function AboutComponent() {
  return (
    <div className='About-Component' id='about'>
        <div className="About-up">
            <div className="Title"><h1>Biz Kimiz?</h1></div>
            <div className="Icons">
                <Link className='link' to='https://www.instagram.com/mobi.menti/' target='_blank' >
                    <img src={Instagram} alt="" />
                </Link>
                <Link className='link' to='https://www.linkedin.com/company/mobimenti/' target='_blank' >
                    <img src={Linkedin} alt="" />
                </Link>
            </div>
        </div>
        <div className="About-down">
            <div className="About-text">
                <p>Biz, Mobimenti olarak ikinin gcnden doan yolculua, bilgi ve deneyimin gcne inanan bir ekibiz. Genlerin kendi potansiyellerini mentorluk yoluyla kefedip gelitirebilecei bir dnya yaratyoruz. Sizleri, yeni olan bu dnyadan haberdar etmeyi de kendimize grev biliyoruz.

                    Dnyada ok fazla uygulanan bir renme modeli olan mentorluk, senin iin bir dnm noktas zellii tayabilir!

                    Bununla birlikte hzla byyen platformumuzda gelimeleri takip etmek ve renmeni hzlandracak eitli kariyer almalar hakknda bilgi almak iin blog sayfamz takip edebilirsin.

                    Unutma; yeni bir a, yeni bir lider, yeni bir kariyer hayali ok uzanda deil. Mentorluk ve eitim programlarmz ok yaknda...</p>
            </div>
        </div>
    </div>
  )
}

export default AboutComponent